import React, { Component } from "react";
import { Player, BigPlayButton } from "video-react";
import "video-react/dist/video-react.css";
export default class JSPlayer extends Component {
  constructor(props, context) {
    super(props, context);

    this.state = {
      source: this.props.id,
      episode: this.props.epi,
      season: this.props.szn,
      current: "",
    };
    this.play = this.play.bind(this);
    this.pause = this.pause.bind(this);
    this.load = this.load.bind(this);
    this.changeCurrentTime = this.changeCurrentTime.bind(this);
    this.seek = this.seek.bind(this);
    this.setMuted = this.setMuted.bind(this);
  }

  componentDidMount() {
    // subscribe state change
    this.player.subscribeToStateChange(this.handleStateChange.bind(this));
  }

  setMuted(muted) {
    return () => {
      this.player.muted = muted;
    };
  }

  handleStateChange(state) {
    // copy player state to this component's state
    this.setState({
      player: state,
    });
  }

  play() {
    this.player.play();
  }

  pause() {
    this.player.pause();
  }

  load() {
    this.player.load();
  }

  changeCurrentTime(seconds) {
    return () => {
      const { player } = this.player.getState();
      this.player.seek(player.currentTime + seconds);
    };
  }

  seek(seconds) {
    return () => {
      this.player.seek(seconds);
    };
  }

  changePlaybackRateRate(steps) {
    return () => {
      const { player } = this.player.getState();
      this.player.playbackRate = player.playbackRate + steps;
    };
  }

  changeVolume(steps) {
    return () => {
      const { player } = this.player.getState();
      this.player.volume = player.volume + steps;
    };
  }

  render() {
    return (
      <div className="video-react-big-play-button-center">
        {this.state.nextEpisode && (
          <div>Your next episode will start shortly.</div>
        )}
        <Player
          ref={(player) => {
            this.player = player;
          }}
          autoPlay
          crossOrigin="true"
          poster={this.props.poster}
          src={this.props.source}
          className="video-react-big-play-button-center"
          fluid={false}
          width={720}
          height={405}
          controlBar={false}
        >
          <source src={this.props.source} />
          <BigPlayButton position="center" />
        </Player>
      </div>
    );
  }
}
