import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Row, Container, Image } from "react-bootstrap";
import Header from "../header/header";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import "./dash.css";
import Featured from "./featured";

class Dashboard extends Component {
  constructor() {
    super();
    this.state = {
      favorites: [],
      recent: [],
      watchlater: [],
      featured: [],
    };
  }

  componentDidMount() {
    let username = localStorage.getItem("user");
    fetch(`${process.env.REACT_APP_URL}/api/favorite/get`, {
      headers: {
        "Content-Type": "application/json",
        Username: `${username}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          favorites: data.reverse(),
        });
      })
      .catch(console.log);

    fetch(`${process.env.REACT_APP_URL}/api/movie/featured`)
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          featured: data,
        });
      })
      .catch(console.log);

    fetch(`${process.env.REACT_APP_URL}/api/recent/get`, {
      headers: {
        "Content-Type": "application/json",
        Username: `${username}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          recent: data.reverse(),
        });
        console.log(data);
      });
    fetch(`${process.env.REACT_APP_URL}/api/watchlater/get`, {
      headers: {
        "Content-Type": "application/json",
        Username: `${username}`,
      },
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({
          watchlater: data.reverse(),
        });
        console.log(data);
      })
      .catch(console.log)

      .catch(console.log);
  }
  render() {
    const responsive = {
      superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5,
        slidesToSlide: 4,
      },
      desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 5,
        slidesToSlide: 4,
      },
      tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 3,
        slidesToSlide: 2,
      },
      mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2,
        slidesToSlide: 2,
      },
    };

    return (
      <Container fluid className="justify-content-center">
        <Header />
        <Featured />

        <Container>
          <Row className="popular-header2">
            <h2>
              <svg
                width="1em"
                height="1em"
                viewBox="0 0 16 16"
                class="bi bi-bookmark-check-fill"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  d="M4 0a2 2 0 0 0-2 2v13.5a.5.5 0 0 0 .74.439L8 13.069l5.26 2.87A.5.5 0 0 0 14 15.5V2a2 2 0 0 0-2-2H4zm6.854 5.854a.5.5 0 0 0-.708-.708L7.5 7.793 6.354 6.646a.5.5 0 1 0-.708.708l1.5 1.5a.5.5 0 0 0 .708 0l3-3z"
                />
              </svg>{" "}
              Recently Watched
            </h2>
          </Row>
          <Carousel
            responsive={responsive}
            ssr={true}
            swipeable={true}
            draggable={false}
          >
            {this.state.recent.map((movie, index) => {
              return (
                <div className="recent">
                  {movie.trailer === null && (
                    <Link
                      to={{
                        pathname: `/tv/${movie.id}`,
                        state: {
                          tmd: movie.tmdbId,
                        },
                      }}
                    >
                      <Image
                        src={`https://image.tmdb.org/t/p/w185/${movie.poster}`}
                        alt={`${movie.title} poster`}
                      />{" "}
                      <br />
                      <span>{movie.title}</span>
                    </Link>
                  )}
                  {movie.trailer !== null && (
                    <Link
                      to={{
                        pathname: `/movie/${movie.id}`,
                        state: {
                          tmd: movie.tmdbId,
                        },
                      }}
                    >
                      <Image
                        src={`https://image.tmdb.org/t/p/w185/${movie.poster}`}
                        alt={`${movie.title} poster`}
                      />{" "}
                      <br />
                      <span>{movie.title}</span>
                    </Link>
                  )}
                </div>
              );
            })}
          </Carousel>
          <hr />
          <Row className="popular-header2">
            <h2>
              <svg
                width="1em"
                height="1em"
                viewBox="0 0 16 16"
                class="bi bi-bookmark-heart-fill"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  d="M4 0a2 2 0 0 0-2 2v13.5a.5.5 0 0 0 .74.439L8 13.069l5.26 2.87A.5.5 0 0 0 14 15.5V2a2 2 0 0 0-2-2H4zm4 4.41c1.387-1.425 4.854 1.07 0 4.277C3.146 5.48 6.613 2.986 8 4.412z"
                />
              </svg>{" "}
              Favorites
            </h2>
          </Row>
          <Carousel
            responsive={responsive}
            ssr={true}
            swipeable={true}
            draggable={false}
          >
            {this.state.favorites.length === 0 && (
              <div>
                Movies and shows you select as your favorites will appear here.
              </div>
            )}

            {this.state.favorites.map((movie, index) => {
              return (
                <div className="favoritewatchlater">
                  {movie.trailer === null && (
                    <Link
                      to={{
                        pathname: `/tv/${movie.id}`,
                        state: {
                          tmd: movie.tmdbId,
                        },
                      }}
                    >
                      <Image
                        src={`https://image.tmdb.org/t/p/w185/${movie.poster}`}
                        alt={`${movie.title} poster`}
                      />{" "}
                      <br />
                      <span>{movie.title}</span>
                    </Link>
                  )}
                  {movie.trailer !== null && (
                    <Link
                      to={{
                        pathname: `/movie/${movie.id}`,
                        state: {
                          tmd: movie.tmdbId,
                        },
                      }}
                    >
                      <Image
                        src={`https://image.tmdb.org/t/p/w185/${movie.poster}`}
                        alt={`${movie.title} poster`}
                      />{" "}
                      <br />
                      <span>{movie.title}</span>
                    </Link>
                  )}
                </div>
              );
            })}
          </Carousel>
          <hr />

          <Row className="popular-header2">
            <h2>
              <svg
                width="1em"
                height="1em"
                viewBox="0 0 16 16"
                class="bi bi-bookmark-check-fill"
                fill="currentColor"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  d="M4 0a2 2 0 0 0-2 2v13.5a.5.5 0 0 0 .74.439L8 13.069l5.26 2.87A.5.5 0 0 0 14 15.5V2a2 2 0 0 0-2-2H4zm6.854 5.854a.5.5 0 0 0-.708-.708L7.5 7.793 6.354 6.646a.5.5 0 1 0-.708.708l1.5 1.5a.5.5 0 0 0 .708 0l3-3z"
                />
              </svg>{" "}
              Watch Later
            </h2>
          </Row>

          <Carousel responsive={responsive}>
            {this.state.watchlater.length === 0 && (
              <div>
                Movies and shows you select to watch later will appear here.
              </div>
            )}
            {this.state.watchlater.map((movie, index) => {
              return (
                <div
                  className="favoritewatchlater"
                  style={{ paddingBottom: "10%" }}
                >
                  {movie.trailer === null && (
                    <Link
                      to={{
                        pathname: `/tv/${movie.id}`,
                        state: {
                          tmd: movie.tmdbId,
                        },
                      }}
                    >
                      <Image
                        src={`https://image.tmdb.org/t/p/w185/${movie.poster}`}
                        alt={`${movie.title} poster`}
                      />{" "}
                      <br />
                      <span>{movie.title}</span>
                    </Link>
                  )}
                  {movie.trailer !== null && (
                    <Link
                      to={{
                        pathname: `/movie/${movie.id}`,
                        state: {
                          tmd: movie.tmdbId,
                        },
                      }}
                    >
                      <Image
                        src={`https://image.tmdb.org/t/p/w185/${movie.poster}`}
                        alt={`${movie.title} poster`}
                      />{" "}
                      <br />
                      <span>{movie.title}</span>
                    </Link>
                  )}
                </div>
              );
            })}
          </Carousel>
        </Container>
      </Container>
    );
  }
}

export default Dashboard;
